$cell-height: 125px;

.radio-show-on-air-personality {

	overflow: hidden;

	position: relative;

	&:last-child:not( :first-child ) {
		//float: none;
	}

	$small-padding: rem-calc( map-get( $grid-column-gutter, 'small' ) ) / 4;

	margin-bottom: $small-padding;

	@media #{$medium-up} {
		margin-bottom: $small-padding * 6;
	}

	a:hover {

		.on-air-personality-title {

			.on-air-personality-title-overlay {

				&:after {

					width: 100%;
					left: 0;

				}

			}

		}

	}

	&, .image-container {

		height: $cell-height;

		@media #{$medium-up} {
			height: $cell-height * 1.5;
		}

	}

	.image-container {

		position: relative;

		@include radio-show-images;

		.image {

			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: transparent;
			transition: all 300ms;
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center center;
			background-color: $tertiary-color;

		}

		.radio-show-headshot, .radio-show-logo {

			max-height: $cell-height;

			@media #{$medium-up} {
				max-height: $cell-height * 1.5;
			}

		}

		.radio-show-headshot {

			right: 0;

		}

		.radio-show-logo {

			top: 30%;
			left: 50%;
			transform: translate( -50%, -30% );

			&.no-headshot {

				@media #{$small-only} {

					right: 0;
					left: auto;
					transform: translateY( -30% );

				}

			}

		}

	}

	.on-air-personality-title {

		position: absolute;
		bottom: 0;
		padding: 0.5em 0 0.5em 1em;
		width: 100%;

		font-weight: 700;

		.on-air-personality-title-overlay {

			background-image: linear-gradient( to right, rgba( 229, 229, 229, 0.60 ), rgba( 229, 229, 229, 0.60 ), rgba( 229, 229, 229, 0.60 ), transparent 90% );
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;

			&:after {

				content: '';
				position: absolute;
				width: 0;
				height: 0.25em;
				display: block;
				right: 0;
				bottom: 0;
				background: $secondary-color;
				transition: width .6s ease;
				-webkit-transition: width .6s ease;

			}

		}

		h1, h2, h3, h4, h5, h6 {

			color: #000;
			font-weight: normal;
			margin-bottom: 0;
			position: relative;

		}

		h6 {
			text-transform: none;
			font-size: 1rem;
		}

		text-align: left;

		color: #000;

	}

}