.button {

	@each $name, $color in $foundation-palette {
		
		&.#{$name} {
			
			border-radius: 2em;
			border-width: 0.25em;
			border-color: $color;

			@include foundation-button-fix( $color );
			
			&:hover, &:focus {
				
				background: $body-background;
				color: $color;
				border-width: 0.25em;
				
			}
			
		}

		&.hollow.#{$name} {

			color: $color;
			border-width: 0.25em;
			font-weight: 600;

			&:hover, &:focus {

				background-color: $color;
				border-color: $color;
				@include light-or-dark-text( $color );

			}

		}

	}

}

.give-btn, .woocommerce a.button, .woocommerce a.button.alt, .woocommerce button.button.alt, .woocommerce input.button, .woocommerce input.button.alt, input[type="submit"], #site-content .tribe-events-button, #site-content #tribe-events .tribe-events-button, .tribe_community_edit #site-content .tribe-button, .tribe_community_list #site-content .tribe-button {

	@include button( false, $secondary-color, scale-color( $secondary-color, $lightness: $button-background-hover-lightness ), 'auto', 'solid' );
	
	border-radius: 2em;
	border-width: 0.25em;
	border-color: $secondary-color;
	
	@include foundation-button-fix( $secondary-color );
	
	text-decoration: none;
	
	&:hover, &:focus {
				
		background: $body-background;
		color: $secondary-color;
		border-width: 0.25em;

	}

}